import { FC, useState } from 'react'
import { getStorage, setStorage } from '../utils/helper'

// data-theme='dark'
const changeTheme = (): string => {
  const $body: HTMLElement = document.body
  const theme = $body.dataset.theme
  let newTheme = ''
  if (!theme) newTheme = 'dark'
  $body.dataset.theme = newTheme
  setStorage('theme', newTheme)
  return newTheme
}

const defaultTheme = getStorage('theme')
const $body: HTMLElement = document.body
$body.dataset.theme = defaultTheme || ''

export const LightAndDrakSwitch: FC = () => {
  const [checked, setChecked] = useState<string>(defaultTheme || '')
  const handleChange = (val: any) => {
    setChecked(changeTheme())
  }

  return (
    <div
      className={`LightAndDrakSwitch ${checked ? 'LightAndDrakSwitch--checked' : ''}`}
      onClick={val => handleChange(val)}>
      <div className='content'>
        <div className='content-a'>
          <img
            src='data:image/png;base64,'
            width='16'
            height='16'
            role='presentation'
            alt=''
          />
        </div>
        <div className='content-b'>
          <img
            src='data:image/png;base64,'
            width='16'
            height='16'
            role='presentation'
            alt=''
          />
        </div>
      </div>
      <div className='control'></div>
    </div>
  )
}
