import { FC } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import { IRoute } from './index'

const AuthRoute: FC<{
  route: IRoute
}> = ({ route }) => {
  if (route.component) {
    return <route.component routes={route.routes} />
  }
  return null
}

export interface IRouteProps {
  routes: IRoute[]
}

export const RouteWithSubRoutes: FC<IRouteProps> = ({ routes }) => {
  return (
    <Switch>
      {routes.map((route, i) => (
        <Route
          key={i}
          path={route.path}
          exact={route.exact}
          render={() =>
            route.redirect ? (
              <Redirect to={{ pathname: route.redirect }} />
            ) : (
              <AuthRoute route={route} />
            )
          }
        />
      ))}
    </Switch>
  )
}
