import moment from 'moment'

const isType = type => obj => ({}.toString.call(obj) === `[object ${type}]`)

export const isFunction = isType('Function')

export const isObject = isType('Object')

export const isArray = window.isArray || isType('Array')

export const isString = isType('String')

export const isNumber = isType('Number')

export const isUndefined = isType('Undefined')

export const isNull = obj => obj === null || obj === undefined || obj === ''

export const getStorage = key => {
  const storage = window.localStorage
  if (key) {
    return JSON.parse(storage.getItem(key))
  }
  return storage
}

export const setStorage = (key, val) => {
  let value = val
  if (isNull(val) || isUndefined(val)) value = ''
  const storage = window.localStorage
  storage.setItem(key, JSON.stringify(value))
}

export const removeStorage = key => {
  const storage = window.localStorage
  storage.removeItem(key)
}

/**
 * 时间转换
 *
 */
const setSize = timeStr => {
  if (Number(timeStr) >= 10) {
    return timeStr
  }
  return `0${timeStr}`
}

export const resetTimestamp = timestamp => {
  const date = timestamp > 0 ? new Date(timestamp) : new Date()
  return {
    year: setSize(date.getFullYear()),
    mon: setSize(date.getMonth() + 1),
    day: setSize(date.getDate()),
    hours: setSize(date.getHours()),
    minu: setSize(date.getMinutes()),
    sec: setSize(date.getSeconds()),
    date: `${setSize(date.getFullYear())}-${setSize(date.getMonth() + 1)}-${setSize(
      date.getDate()
    )}`,
    time: `${setSize(date.getHours())}:${setSize(date.getMinutes())}:${setSize(date.getSeconds())}`,
    timeHM: `${setSize(date.getHours())}:${setSize(date.getMinutes())}`,
  }
}

export const timeformatDate = (timestamp, gapStr = '-') => {
  const { year, mon, day } = resetTimestamp(timestamp)
  return year + gapStr + mon + gapStr + day
}

export const timeformatTime = (timestamp, gapStr = ':', noSec = false) => {
  const { hours, minu, sec } = resetTimestamp(timestamp)
  if (noSec) {
    return hours + gapStr + minu
  }
  return hours + gapStr + minu + gapStr + sec
}

export const timeformatDateAndTime = (timestamp, gapStr = '-', gap2Str = ':') => {
  const { year, mon, day, hours, minu, sec } = resetTimestamp(timestamp)
  return `${year + gapStr + mon + gapStr + day} ${hours}${gap2Str}${minu}${gap2Str}${sec}`
}

export const timeformatGapTime = start => {
  const startTime = new Date(start) // 开始时间
  const endTime = new Date() // 结束时间
  const usedTime = endTime - startTime // 相差的毫秒数
  const days = Math.floor(usedTime / (24 * 3600 * 1000)) // 计算出天数
  const leavel = usedTime % (24 * 3600 * 1000) // 计算天数后剩余的时间
  const hours = Math.floor(leavel / (3600 * 1000)) // 计算剩余的小时数
  const leavel2 = leavel % (3600 * 1000) // 计算剩余小时后剩余的毫秒数
  const minutes = Math.floor(leavel2 / (60 * 1000)) // 计算剩余的分钟数
  return `${days}天${hours}时${minutes}分`
}

/*
 * @desc 函数防抖  指触发事件后在 n 秒内函数只能执行一次，如果在 n 秒内又触发了事件，则会重新计算函数执行时间。
 * @param func 函数
 * @param wait 延迟执行毫秒数 默认为1000
 * @param immediate true 表立即执行，false 表非立即执行 默认为true
 */
export function debounce(func, wait = 1000, immediate = true) {
  let timeout
  function resultFunc() {
    const context = this
    // eslint-disable-next-line prefer-rest-params
    const args = arguments
    if (timeout) clearTimeout(timeout)
    if (immediate) {
      const callNow = !timeout
      timeout = setTimeout(() => {
        timeout = null
      }, wait)
      if (callNow) func.apply(context, args)
    } else {
      timeout = setTimeout(() => {
        func.apply(context, args)
      }, wait)
    }
  }
  return resultFunc
}

/*
 * @desc 函数节流  指连续触发事件但是在 n 秒中只执行一次函数。节流会稀释函数的执行频率。
 * @param func 函数
 * @param wait 延迟执行毫秒数
 * @param type 1 表时间戳版，2 表定时器版 默认为1
 */
export function throttle(func, wait = 1000, type = 1) {
  let previous = 0
  let timeout
  function resultFunc() {
    const context = this
    // eslint-disable-next-line prefer-rest-params
    const args = arguments
    if (type === 1) {
      const now = Date.now()
      if (now - previous > wait) {
        func.apply(context, args)
        previous = now
      }
    } else if (type === 2) {
      if (!timeout) {
        timeout = setTimeout(() => {
          timeout = null
          func.apply(context, args)
        }, wait)
      }
    }
  }
  return resultFunc
}

/**
 * 获取url中参数的key和value
 * 返回json对象
 */
export function getUrlParam() {
  const str = window.location.search
  const parms = {}
  if (str === '' || str === '?') return parms
  const Params = str.substr(1).split('&')
  for (let i = 0; i < Params.length; i += 1) {
    const list = Params[i].split('=')
    const p = list[1]
    parms[list[0]] = p
  }
  return parms
}

/**
 * 数组转对象，key为传入的参数key，value为数组中对应的项
 * @param arr: Array
 * @param key: String
 */
export function arrToObj(arr, key) {
  let obj = {}
  obj = arr.reduce((cur, next) => {
    cur[next[key]] = next
    return cur
  }, obj)
  return obj
}

/**
 * 对象转数组
 * @param obj: Object
 */
export function objToArr(obj) {
  return Object.values(obj)
}

/**
 * 阻止冒泡
 * @param e: event
 */
export function stopPropagation(e) {
  if (window.event) {
    window.event.cancelBubble = true
  } else {
    e.stopPropagation()
  }
}

// JSON序列化克隆
export function JSONDeepClone(arg) {
  return JSON.parse(JSON.stringify(arg))
}

export function blobDownload(res, fileName) {
  if (!res || !fileName) {
    console.error('参数错误')
    return
  }
  const link = document.createElement('a')
  const blob = new Blob([res], { type: 'application/vnd.ms-excel' })
  link.style.display = 'none'
  link.href = URL.createObjectURL(blob)
  link.download = fileName + '.xlsx' // 下载的文件名
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
}

export function getDictByParms(list, key, value, targetKey) {
  if (!isArray(list) || list.length < 1 || value === null || value === '') return ''
  const target = list.find(item => item[key] === value)
  return target ? target[targetKey] : ''
}

//去除字符串空格
export function trim(str, type) {
  type = type || 1
  switch (type) {
    case 1:
      return str.replace(/\s+/g, '')
    case 2:
      return str.replace(/(^\s*)|(\s*$)/g, '')
    case 3:
      return str.replace(/(^\s*)/g, '')
    case 4:
      return str.replace(/(\s*$)/g, '')
    default:
      return str
  }
}

//设置cookie
export function setLoginCookie(userName, passWord, exdays) {
  let exdate = new Date() //获取时间
  exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) //保存的天数
  window.document.cookie = `userName=${userName};path=/;expires=${exdate.toGMTString()}`
  window.document.cookie = `passWord=${passWord};path=/;expires=${exdate.toGMTString()}`
}

//读取cookie
export function getLoginCookie() {
  if (document.cookie.length > 0) {
    let arr = document.cookie.split('; ')
    for (let i = 0; i < arr.length; i++) {
      let arr2 = arr[i].split('=')
      if (arr2[0] === 'userName') {
        this.userName = arr2[1]
      } else if (arr2[0] === 'passWord') {
        this.password = arr2[1]
        this.rememberPassword = true
      }
    }
  }
}

//清除cookie
export function clearLoginCookie() {
  setLoginCookie('', '', -1)
}

export function filterFormDate(date) {
  if (date) {
    return moment(date.substring(0, 11), 'YYYY-MM-DD')
  }
  return ''
}

export function formatFormDate(date) {
  if (date && typeof date === 'object') {
    return date.format('YYYY-MM-DD')
  }
  return ''
}

export function filterFormQuarterDate(year, quarter) {
  if (year && quarter) {
    return moment(`${year}-Q${quarter}`, 'YYYY-Q')
  }
  return undefined
}

export function formatFormQuarterDate(quarterDate) {
  if (quarterDate) {
    return {
      year: quarterDate.year(),
      quarter: quarterDate.quarter(),
    }
  }
  return {
    year: '',
    quarter: '',
  }
}

export function computerColumsWidth(colums) {
  let width = 300
  colums.forEach(colum => {
    let columWidth = 40
    if (colum.title && typeof colum.title === 'string') {
      columWidth += colum.title.length * 20
      if (colum.sort) columWidth += 30
    }
    width += columWidth
  })
  return width
}
