import Axios from 'axios'
import { message } from 'antd'
import { isString, isNumber, isObject, getStorage, removeStorage } from '../utils/helper'

/**
 * Parse the URL routing
 * `parse('user/query', '?name=j&age=21')` => `user/query?name=j&age=21`
 * `parse('user/query', { name: 'j', age: '21' })` => `user/query?name=j&age=21`
 * `parse('user/:userId/info/:infoId', { userId: 1, infoId: 2 })` => `user/1/info/2`
 *
 * @param {string} path The request path
 * @param {Object} params The data which need to be parse
 */
export const parse = (path, params) => {
  let newPath = path
  if (isString(params)) return `${newPath}/${params}`
  if (isNumber(params)) return `${newPath}/${params}`
  if (isObject(params)) {
    if (/:[a-zA-Z]+/.test(newPath)) {
      Object.keys(params).forEach(key => {
        newPath = newPath.replace(new RegExp(`:${key}`, 'g'), params[key])
      })
    } else {
      let search = newPath.indexOf('?') > -1 ? '&' : '?'
      let counter = 0
      Object.keys(params).forEach(key => {
        if (counter) search += '&'
        search += `${key}=${params[key]}`
        counter += 1
      })
      return newPath + search
    }
  }
  return newPath
}

const getRequestSetting = (type, path, data, isBlob) => {
  const setting = {
    cache: false,
    url: path,
    method: type,
    contentType: 'application/json',
    dataType: 'json',
    timeout: 1000 * 60,
  }

  // Add data
  if (data) setting.data = data
  if (isBlob) setting.responseType = 'blob'

  // Add authorization
  const authorization = getStorage('authorization')
  if (authorization) setting.headers = { authorization: `Bearer_${authorization}` }
  return setting
}

const doAjax = (type, path, data, isBlod) =>
  new Promise((resolve, reject) => {
    Axios(getRequestSetting(type, path, data, isBlod))
      .then(response => {
        if (response.status === 200) {
          if (isBlod) {
            resolve(response.data)
          } else {
            if (response.data.code === 200) {
              resolve(response.data)
            } else {
              message.error(response.data.msg || '系统错误')
              reject(response.data)
            }
          }
        } else {
          message.error(response.data.msg || '系统错误')
          reject(response.data)
        }
      })
      .catch(err => {
        if (err.response?.status === 401) {
          removeStorage('authorization')
          window.location.reload()
        } else {
          message.error(err.response?.data?.msg || '系统错误')
          reject(err.response?.data)
        }
      })
  })

/**
 * Restful APIs
 * It's a class for connecting with Server via API
 * @param path The URL of the API request
 * @param data The data
 * @param params The params which will be prased like `?a=1&b=2` and added to the end of the path
 *
 */
class Rest {
  /**
   * Post
   * @param {string} path The URL of the API request
   * @param {Object} data The data
   * @param {Object} params The params which will be prased like `?a=1&b=2` and added to the end of the path
   */
  post(path, data, params) {
    return doAjax('POST', params ? parse(path, params) : path, data)
  }

  /**
   * Delete
   * @param {string} path The URL of the API request
   * @param {Object} data The data
   * @param {Object} params The params which will be prased like `?a=1&b=2` and added to the end of the path
   */
  delete(path, data, params) {
    return doAjax('DELETE', params ? parse(path, params) : path, data)
  }

  /**
   * Put
   * @param {string} path The URL of the API request
   * @param {Object} data The data
   * @param {Object} params The params which will be prased like `?a=1&b=2` and added to the end of the path
   */
  put(path, data, params) {
    return doAjax('PUT', params ? parse(path, params) : path, data)
  }

  /**
   * Get
   * @param {string} path The URL of the API request
   * @param {Object} data The data which will be prased like `?a=1&b=2` and added to the end of the path
   */
  get(path, data) {
    return doAjax('GET', data ? parse(path, data) : path, null)
  }

  blob(path, data) {
    return doAjax('GET', data ? parse(path, data) : path, null, true)
  }

  blobPost(path, data, params) {
    return doAjax('POST', params ? parse(path, params) : path, data, true)
  }
}

const rest = new Rest()
export default rest
