import { FC, useEffect, useState } from 'react'
import MarkdownIt from 'markdown-it'
import { useRequest } from 'ahooks'
import { service_artice } from '../../../services/service'
import { useParams } from 'react-router-dom'
import { timeformatDate } from '../../../utils/helper'

interface IArticeListItem {
  id: number
  author_id: number
  create_time: number
  is_top: number
  pv: number
  tags: string[]
  title: string
  intro: string
  md_content: string
}

export const MdContent: FC<{ content: string }> = ({ content }) => {
  const [html, sethtml] = useState('')

  useEffect(() => {
    if (content) {
      let converter = new MarkdownIt()
      let html = converter.render(content)
      sethtml(html)
    }
  }, [content])

  return (
    <div className='MdContent'>
      <div dangerouslySetInnerHTML={{ __html: html }}></div>
    </div>
  )
}

export const ArticeDetail: FC = () => {
  const { data, run: run_getDetail } = useRequest(service_artice.detail, { manual: true })
  const params = useParams<{ id: string }>()

  useEffect(() => {
    if (params.id) {
      run_getDetail({ id: params.id })
    }
  }, [params, run_getDetail])

  const detail: IArticeListItem | null = data?.data

  return detail ? (
    <div className='ArticeDetail'>
      <header>
        <h1>{detail.title}</h1>
        <small>Created Time：{timeformatDate(detail.create_time)}</small>
      </header>
      <MdContent content={detail.md_content} />
    </div>
  ) : null
}
