import { FC } from 'react'

import { NotFind } from '../pages/404'
import { Artice } from '../pages/artice/index'
import { ArticeDetail } from '../pages/artice/detail'
import { ArticeList } from '../pages/artice/list'

export interface IRoute {
  path: string
  component?: FC<any>
  routes?: IRoute[]
  redirect?: string
  exact?: boolean
  auth?: { login: boolean }
}

export const routesList: IRoute[] = [
  {
    path: '/',
    exact: true,
    redirect: '/artice/list',
  },
  {
    path: '/artice',
    component: Artice,
    routes: [
      {
        path: '/artice',
        exact: true,
        redirect: '/artice/list',
      },
      {
        path: '/artice/list',
        component: ArticeList,
      },
      {
        path: '/artice/:id',
        component: ArticeDetail,
      },
    ],
  },
  {
    path: '/404',
    component: NotFind,
  },
  {
    path: '*',
    redirect: '/404',
  },
]
