import Rest from './rest'
import { artice } from './api'

export const service_artice = {
  add: params => Rest.post(artice.restful, params),
  update: (params, data) => Rest.put(artice.restful_Id, data, params),
  del: params => Rest.delete(artice.restful_Id, params),
  list: params => Rest.get(artice.restful, params),
  detail: data => Rest.get(artice.restful_Id, data),
}
