import { FC } from 'react'
import { useRequest } from 'ahooks'
import { service_artice } from '../../../services/service'
import { Link } from 'react-router-dom'
import { timeformatDate } from '../../../utils/helper'

interface IArticeListItem {
  id: number
  author_id: number
  create_time: number
  is_top: number
  pv: number
  tags: string[]
  title: string
  intro: string
}

const ArticeListItem: FC<IArticeListItem> = props => {
  return (
    <article>
      <header>
        <h3>
          <Link to={`/artice/${props.id}`}>{props.title}</Link>
        </h3>
        <small>Created Time：{timeformatDate(props.create_time)}</small>
      </header>
      {props.intro && <p>props.intro</p>}
    </article>
  )
}

export const ArticeList: FC = () => {
  const { data } = useRequest(service_artice.list)

  const list = data?.data || []
  return (
    <div className='ArticeList'>
      {list.map((item: IArticeListItem) => (
        <ArticeListItem key={item.id} {...item} />
      ))}
    </div>
  )
}
